import { Link } from "gatsby";
import PropTypes from "prop-types";
import React from "react";
import styled from "styled-components";
import { respondTo } from "./shared/_respondTo";

const HeaderContainer = styled.header`
  background-color: #3ac569;
  ${respondTo.xs`
    margin-bottom:0;
  `}
`;

const HeaderContent = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 10vh;
  margin: 0 auto;
  max-width: 960;
  ${respondTo.xs`
    margin:0;
    padding:2rem 1.5rem;
  `}
`;

const Header = ({ siteTitle }) => (
  <HeaderContainer>
    <HeaderContent>
      <h1
        style={{
          fontSize: "56px",
          margin: 0,
          textShadow: "1px 1px 2px #52616a",
          textAlign: "center",
          fontFamily: "SDSamliphopangche_Outline",
        }}
      >
        <Link
          to="/"
          style={{
            color: `white`,
            textDecoration: `none`,
          }}
        >
          {siteTitle}
        </Link>
      </h1>
    </HeaderContent>
  </HeaderContainer>
);

Header.propTypes = {
  siteTitle: PropTypes.string,
};

Header.defaultProps = {
  siteTitle: ``,
};

export default Header;
