import React from "react";
import styled from "styled-components";
import { Link, useStaticQuery, graphql } from "gatsby";
import { respondTo } from "./shared/_respondTo";

const Contaner = styled.div`
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  left: 0;
  ${respondTo.lg`
    padding:1rem 0;
  `}
`;

const LinkContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0.1rem 1rem;
  ${respondTo.lg`
    display: flex;
    flex-direction: row;
    overflow:auto;
    a {
      display:block;
      background-color:#efefef;
      border-radius:1.5rem;
      margin: 0.5rem;
      padding: 0.5rem 0.5rem;
      white-space: nowrap;
    }
    a:hover{
      background-color:#cff0da;
    }
  `}
`;

export const TagList = () => {
  const data = useStaticQuery(graphql`
    {
      allMarkdownRemark {
        totalCount
        group(field: frontmatter___tags) {
          tag: fieldValue
          totalCount
        }
      }
    }
  `);
  const tagList = data.allMarkdownRemark.group || null;
  return (
    <Contaner>
      <h3 style={{ color: "#55967e" }}>태그목록</h3>
      <LinkContainer>
        <Link key={0} to={`/blog`}>
          {`전체글(${data.allMarkdownRemark.totalCount})`}
        </Link>
        {tagList
          ? tagList.map((e, idx) => (
              <Link
                key={idx + 1}
                to={`/?tagName=${e.tag}`}
              >{`${e.tag}(${e.totalCount})`}</Link>
            ))
          : null}
      </LinkContainer>
    </Contaner>
  );
};
